Vision和Scarlet Witch在Giant-Size Avengers＃4（1975年6月）结婚。这对夫妇出演了有限的剧集 The Vision和Scarlet Witch＃1-4（1982年11月 - 1983年2月），由作家Bill Mantlo和铅笔乐手 Rick Leonardi主演。接下来是第二卷，编号为＃1-12（1985年10月 - 1986年9月），由Steve Englehart撰写并由Richard Howell撰写，其中Scarlet Witch生下了通过魔法手段与Vision构思的双胞胎男孩。</blockquote>
